import { z } from 'zod';
import { VersionKnowledgeBaseDTO } from '../version/version-knowledge-base.dto.js';
import { ProjectAIAssistSettingsDTO } from './project-ai-assist-settings.dto.js';
import { ProjectCustomThemeDTO } from './project-custom-theme.dto.js';
import { ProjectMemberDTO } from './project-member.dto.js';
import { ProjectPrivacy } from './project-privacy.enum.js';
import { ProjectReportTagDTO } from './project-report-tag.dto.js';
import { ProjectStickerDTO } from './project-sticker.dto.js';
export const ProjectDTO = z
    .object({
    _id: z.string(),
    nlu: z.string().optional(),
    type: z.string().optional(),
    name: z.string(),
    image: z.string().optional(),
    teamID: z.string(),
    members: z.array(ProjectMemberDTO),
    privacy: z.nativeEnum(ProjectPrivacy).optional(),
    platform: z.string(),
    _version: z.number().optional(),
    linkType: z.string().optional(),
    stickers: z.array(ProjectStickerDTO).optional(),
    creatorID: z.number(),
    updatedBy: z.number().optional(),
    createdAt: z.string().nullable().optional(),
    updatedAt: z.string().optional(),
    prototype: z.record(z.unknown()).optional(),
    apiPrivacy: z.nativeEnum(ProjectPrivacy).optional(),
    devVersion: z.string().optional(),
    liveVersion: z.string().optional(),
    reportTags: z.record(ProjectReportTagDTO).optional(),
    platformData: z.record(z.unknown()),
    customThemes: z.array(ProjectCustomThemeDTO).optional(),
    knowledgeBase: z.optional(VersionKnowledgeBaseDTO).describe('@deprecated use version.knowledgeBase'),
    aiAssistSettings: z.optional(ProjectAIAssistSettingsDTO),
})
    .strict();
