import { z } from 'zod';
import { ProgramCommandDTO } from './program-command.dto.js';
import { ProgramLineDTO } from './program-line.dto.js';
export const ProgramDTO = z
    .object({
    _id: z.string(),
    name: z.string(),
    lines: z.record(ProgramLineDTO),
    startId: z.string(),
    legacyId: z.string().optional(),
    commands: ProgramCommandDTO.array(),
    diagramID: z.string(),
    versionID: z.string(),
    variables: z.string().array(),
})
    .strict();
