import { z } from 'zod';
import { PromptDTO } from '../../prompt/prompt.dto.js';
import { JSONResponseVariantDTO, PromptResponseVariantDTO, TextResponseVariantDTO } from './response-variant.dto.js';
export const PromptResponseVariantWithPromptDTO = PromptResponseVariantDTO.extend({
    prompt: PromptDTO,
}).strict();
export const AnyResponseVariantWithDataDTO = z.union([
    JSONResponseVariantDTO,
    TextResponseVariantDTO,
    PromptResponseVariantWithPromptDTO,
]);
